@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap');

.col{
    width: 200px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #000;
    border-radius: 20px;
    margin-left: 30px;
    margin-bottom: 30px;
    box-shadow: 11px 11px 40px rgba(44, 39, 39, 0.658);
}

.col div{
    background-color: #fff;
    width: 100%;
    height: 150px;
    display: flex;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.col div img{
    width: 100%;
    object-fit: contain;
}

.col h2{
    color: #fff;
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
}